html,body{
    min-height: 100vh;
    background-color: #fafafa;
    color: #505050;
}
.small{
    font-size: 12px;
}
.gray{
    color: gray;
}
.list-header{
    font-size:16px;
    font-weight:800;
    color:#00baad;
    padding:10px 0;
}
.list-body .item{
    border-radius: 5px;
    background-color: #fff;
    padding: 12px 20px;
    box-shadow: 0 0 7px rgba(0,0,0,.1);
    margin-bottom: 10px;
    display: flex;
}
.list-body .item .item-body{
    flex: 1;
}
.list-body .item .item-footer{
    width: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.list-body .item .item-body .desc{
    color: #a6a6a6;
    font-size:12px;
}
.icon-calendar{
    width:50px;
    height: 40px;
    border: solid 1px #00baad;
    text-align: center;
    font-size: 14px;
}
.icon-calendar em{
    display: block;
    background-color: #00baad;
    font-style: normal;
    font-size: 5px;
    color: white;
}
.icon-calendar-expired{
    border: solid 1px #ccc;
}
.icon-calendar-expired em{
    display: block;
    background-color: #ccc;
}
.hero{
    display: flex;
    flex-direction: row;
    padding: 10px 40px;
    background-size: cover;
    color: white;
    text-shadow: 0 0 2px rgba(0,0,0,.5);
    position: relative;
}
.hero::after{
    content: '';
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,.2);
    position: absolute;
    left: 0;
    z-index: 1;
    top: 0;
}
.hv-center{
    display: flex;
    align-items: center;
    justify-content: center;
}
.hero-body{
    flex: 1;
    z-index: 2;
}
.hero .hero-footer{
    display: flex;
    align-items: center;
    width: 100px;
    justify-content: flex-end;
    flex-direction: column;
    z-index: 2;
}
.hero-footer .star{
    font-size: 30px;
    color: orange;
}
.hero .title{
    font-size: 30px;
    font-weight: 800;
    margin-top: 100px;
}
.panel{
    padding: 10px;
}
.panel .title{
    font-size: 14px;
    padding: 20px 0;
    display: flex;
}
.panel .item{
    flex: 1;
    font-weight: 800;
}
.panel .title-footer{
    max-width: 200px;
    text-align: right;
}

#map{
    height: 250px;
    width: 100%;
}
