html {
    -webkit-tap-highlight-color: transparent;
    font-family: Helvetica Neue,Helvetica,PingFang SC,Hiragino Sans GB,Microsoft YaHei,Arial,sans-serif;
  }
  
  body {
    margin: 0;
  }
  
  a {
    text-decoration: none;
  }
  
  a:focus,
  input:focus,
  button:focus,
  textarea:focus {
      outline: none;
  }
  
  ul {
    margin: 0;
    padding: 0;
    list-style: none;
  }
  
  ol {
    margin: 0;
    padding: 0;
    list-style: decimal;
  }
  
  input,
  button,
  textarea {
    font: inherit;
  }
  
  .clearfix::after {
    content: '';
    display: table;
    clear: both;
  }
  
  .ellipsis {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  
  .multi-ellipsis {
    overflow : hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
  }